
#landing-page-content, .landing-plate-wrapper {
    display: grid; 
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
  
  }
  
  body{    
    max-width: 100%;
    overflow-x: hidden;
  }
  
  
  p{
    margin: 0;
    padding: 0;
  }
  
    /* unvisited link */
    a:link {
      color: black;
    }
    
  
    a:visited {
      color: black;
    }
  
    a:hover{
      color:white
    }
  
  
  
  .App{
    width: 100vw;
    height: 300vh;
    background-color:#f1edeb;
   
  }
  
  
  
  .sectionTitle{
  
    font-family: ITCGaramond; 
    margin-top: 2rem;
    font-size: 3rem;
  
    text-align: center;
  }
  
  .text-left{
    text-align: left;
  }
  .sectionTitleinPlate{
  
    display: inline-block;
  
    font-family: RAW; 
    margin: 2rem auto;
  
    font-size: 3rem;
    padding: 0.5rem 0.8rem;
  
    text-align: center;
    color:white;
    background-color: black;
  }
  
  
  
  .landing-plate-plate{
  
    font-family: rift,sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: 4rem;
  
  
    padding: 0 1rem;
    margin: 0.5rem 0;
    
    display: inline-block;
  
    box-sizing: border-box;
  
    color: #f1edeb;
  
    z-index: 200;
    
  }
  /* 
  https://css-tricks.com/things-watch-working-css-3d/ */
  .landing-plate-wrapper{
    width: 100vw;
    height: 100vh;
    /* transform-style: preserve-3d; */
    transform-origin: top left;
    perspective: 40em;
    position: fixed;
    z-index: 200;
  }
  
  #plate-1, #plate-3, #plate-4{
    margin-right: 3%;
  }
  
  #plate-2{
    margin-left: 3%;
  }
  
  #plate-1{
    
    grid-row: 3;
    grid-column: 3;
    justify-self: end;
    background-color: black;
    transform-origin: top right;
    transform: rotateY(25deg);
    text-shadow: -1px 0px #c6c6c6;
    
  }
  
  #plate-2{
    /* position: absolute; */
    grid-row: 5;
    grid-column: 4;
    justify-self: start;
    background-color: #226600;
    transform-origin: top left;
    transform: rotateY(-25deg);
    text-shadow: 1px 0px #c6c6c6;
  }
  
  #plate-3{
  
    grid-row: 7;
    grid-column: 3;
    justify-self: end;
    background-color: #8a5100;
    transform-origin: top right;
    transform: rotateY(25deg);
    text-shadow: -1px 0px #c6c6c6;
  
  }
  
  #plate-4{
    grid-row: 8;
    grid-column: 3;
    justify-self: end;
    background-color: #226600;
    transform-origin: top right;
    transform: rotateY(25deg);
    text-shadow: -1px 0px #c6c6c6;
  
  }
  
  .landing-pole{
    height: 100vh;
    
    /* background-color: #f5e56d; */
    /* background-color: white; */
    grid-row: 1/-1;
    z-index: 10;
    
  }
  
  #landing-pole-1{
    width: 1px;
    grid-column:3;
    justify-self: end;
  
    position: relative;
  
  }
  
  #landing-pole-2{
    width: 1px;
    grid-column:4;
    justify-self: start;
  }
  
  #landing-page-wrapper{
    position: absolute;
    top:0;
    left:0;
    z-index: -10;
  }
  
  #landing-page-content{
   position:absolute;
   top: 0px;
   left:0px;
   width: 100vw;
   height: 100vh;
  }
  
  #landing-page-subtitle{
    width: 100vw;
    height: 100vh;
    position: absolute;
    font-family: ITCGaramond;
    grid-row: 9;
    margin-top: 2rem;
    grid-column: 7;
    font-size: 3rem;
  
    text-align: left;
    z-index: 10;
  
   
  }
  
  #landing-page-text{
   width: 40vw;
   height: 50vh;
   /* background-color: white; */
   position:absolute;
  
   /* transform: translateY(-100); */
   grid-row: 12;
   grid-column: 7;
   z-index: 10;
  }
  
  
  .text-center{
    text-align: center;
    padding:2rem 20%;
  }
  
  